<template>
  <div>
    <div id="printDocument">
      <div class="row">
        <div class="col-md-6">
          <h4 class="text-black mb-3">
            <strong>{{ $store.state.appName }}</strong>
            <br>
            <br>
            <img class="mr-2" src="logos/Fred_Mac.png" height="50" alt="Amazon">
          </h4>
        </div>
        <div class="col-md-6 text-right">
          <h4 class="text-black mb-3">
            <strong>Invoice Info</strong>
          </h4>
          <p>
            <a class="font-size-20" href="javascript: void(0);">{{ invoicePageData[0] !== undefined ? invoicePageData[0].customer_name : ''}}</a>
            <br>
            <span class="font-size-20">{{ invoicePageData[0] !== undefined ? invoicePageData[0].business_name : ''}}</span>
          </p>
          <address>
            {{invoicePageData[0] !== undefined ? invoicePageData[0].address : ''}}
            <br>
            <abbr class="mr-2" title="Phone">Phone:</abbr>
            {{ invoicePageData[0] !== undefined ? invoicePageData[0].phone : ''}}
            <br>
            <abbr class="mr-2" title="Phone">Email:</abbr>
            {{ invoicePageData[0] !== undefined ? invoicePageData[0].email : ''}}
            <br>
          </address>
  <!--        <span>Invoice Date: {{invoiceData.invoiceDate}}</span>-->
  <!--        <br>-->
  <!--        <span>Due Date: {{invoiceData.invoiceDueDate}}</span>-->
  <!--        <br>-->
          <br>
        </div>
      </div>
      <a-table
        :columns="columns"
        :dataSource="invoicePageData"
        :pagination="false"
        class="utils__scrollTable"
        :scroll="{ x: '100%' }"
      >
        <span slot="total_usd" slot-scope="text">${{text}}</span>
        <span slot="total_ngn" slot-scope="text">₦{{text}}</span>
      </a-table>
    </div>
    <div class="text-right clearfix mt-5"></div>
    <div class="text-right">
      <a-button class="mr-2" @click="$emit('goBack')" type="primary">
        <a-icon type="check-circle-o"/>Back
      </a-button>
      <a-button @click="print" type="default">
        <a-icon type="printer"/>Print
      </a-button>
    </div>
  </div>
</template>

<script>
import { invoiceData } from './data.json'
import Printd from 'printd'

const { invoiceContent } = invoiceData
const columns = [
  {
    title: '#',
    dataIndex: 'id',
    key: 'id',
  },
  {
    title: 'Customer Name',
    dataIndex: 'customer_name',
    key: 'customer_name',
  },
  {
    title: 'CTN',
    dataIndex: 'shipping_ctn',
    key: 'shipping_ctn',
  },
  {
    title: 'CBM',
    dataIndex: 'shipping_cbm',
    key: 'shipping_cbm',
  },
  {
    title: 'Total in USD',
    dataIndex: 'total_usd',
    key: 'total_usd',
  },
  {
    title: 'Total in Naira',
    dataIndex: 'total_ngn',
    key: 'total_ngn',
  },
]

export default {
  name: 'CuiInvoice',
  data: function () {
    return {
      columns,
      invoiceData,
      invoiceContent,
      invoiceName: '',
      invoicePageData: [],
    }
  },
  props: {
    data: Object,
  },
  mounted() {
    this.invoiceName = this.$route.params.invoice_number
    this.setInvoice()
  },
  methods: {
    print() {
      this.loadingPrint = true
      console.log('printing')
      const d = new Printd()
      const cssText = `
        table {
          font-family: arial, sans-serif;
          border-collapse: collapse;
          width: 100%;
          margin-top: 20px;
        }

        td, th {
          border: 1px solid #dddddd;
          text-align: left;
          padding: 8px;
        }

        th {
          padding: 20px;
          background-color: #fafafa;
          border-top-left-radius: 3px;
          border-top-right-radius: 3px;
        }

        tr:nth-child(even) {
          background-color: #dddddd;
        }
      `
      const printCallback = ({ launchPrint }) => launchPrint()

      d.print(document.getElementById('printDocument'), [cssText], '', printCallback)

      // document.getElementById('printDocument').classList.add('sheet')
      // document.getElementById('printDocument').classList.add('padding-10mm')
      // // console.log(printContents)
      // var originalContents = document.body.innerHTML
      // document.body.innerHTML = printContents
      // window.print()
      // document.body.innerHTML = originalContents
    },
    setInvoice() {
      const dt = this.$props.data
      const data = []
      const userData = {
        customer_name: dt.customer_name.name,
        address: dt.address,
        business_name: dt.business_name,
        phone: dt.phone,
        email: dt.email,
        shipping_phone: dt.shipping_phone,
        shipping_ctn: dt.shipping_ctn,
        shipping_cbm: dt.shipping_cbm,
        total_usd: dt.total_usd,
        total_ngn: dt.total_ngn,
        container: dt.container,
        id: dt.id,
        key: dt.id,
      }
      data.push(userData)
      this.invoicePageData = data
    },
  },
}
</script>
